body{
    background-color: hsl(0, 0%, 13%);
    color: aliceblue;
    font-family: Arial, Helvetica, sans-serif;
    margin: 0;
    text-align: center;
}
.to-do-list{
    text-align: center;
    font-size: 1.5rem;
    margin-top: 80px;
}
h1{
    font-size: 4rem;
}
button{
    color: white;
    font-size: 1.5rem;
    margin-left: 10px;
    padding: 5px;
    border: none;
    border-radius: 5px;
}
.addButton{
    background-color: hsla(120, 77%, 36%, 0.758);
}
.addButton:hover{
    background-color: hsla(120, 77%, 26%, 0.758);
}
.upButton, .downButton{
    background-color: hsla(222, 93%, 55%, 0.979);
}
.upButton:hover, .downButton:hover{
    background-color: hsla(222, 93%, 45%, 0.979);
}
.deleteButton{
    background-color: hsla(0, 100%, 50%, 0.786);
}
.deleteButton:hover{
    background-color: hsla(0, 100%, 40%, 0.786);
}
input[type="text"]{
    font-size: 1.5rem;
    padding: 10px;
    border-radius: 5px;
    color: rgb(119, 119, 119);
}
ul{
    list-style: none;
    padding: 0;
}
li{
    font-size: 2rem;
    display: flex;
    color: black;
    align-items: center;
    font-weight: bold; 
    background-color: whitesmoke;
    border: 3px bold black;
    border-radius: 5px;
    padding: 15px;
    margin: 0 5px 0 5px;
}
.text{
    flex: 1;
}
.errorMessage{
    color: red;
    font-size: 0.8rem;
    margin: 3px;
    position: absolute;
    left:95px
}